@use '../util' as *;

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: rem(30);

  @include breakpoint(medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include breakpoint(large) {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  flex: 1;
  background: var(--dark-card);
  color: var(--dark-text1);
  padding: rem(25);
  text-align: center;
  border-radius: rem(5);

  &--facebook {
    border-top: rem(5) solid var(--facebook);
  }

  &--twitter {
    border-top: rem(5) solid var(--twitter);
  }

  &--instagram {
    padding-top: rem(30);

    &::before {
      content: '';
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      right: 0;
      height: rem(5);
      background: linear-gradient(225deg,
          var(--instagram-start),
          var(--instagram-middle) 50.91%,
          var(--instagram-end) 100%)
    }
  }

  &--youtube {
    border-top: rem(5) solid var(--youtube);
  }

  &__platform {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: rem(28);
  }

  &__icon {
    margin-right: rem(8);

    &--facebook {}

    &--twitter {}

    &--instagram {}

    &--youtube {}
  }

  &__username {
    font-size: rem(12);
    font-weight: 700;
  }

  &__followers {
    margin-bottom: rem(25);
  }

  &__count {
    color: var(--light-bg);
    font-weight: 700;
    letter-spacing: rem(-2);
    line-height: 1;
    margin-bottom: rem(4);

    &--big {
      font-size: rem(56);
    }

    &--small {}
  }

  &__label {
    font-size: rem(12);
    letter-spacing: rem(5);
    font-weight: 400;
    color: var(--dark-text1);
    text-transform: uppercase;
  }

  &__change {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: rem(12);
    font-weight: 700;

    &--up {
      @extend .card__change;
      color: var(--limegreen);
    }

    &--down {
      @extend .card__change;
      color: var(--brightred);
    }

    img {
      margin-right: rem(4);

    }
  }

  &__number {}
}